import axios from 'axios'
import _ from 'lodash';

//货币枚举
export function currencyEnum(type){
    switch (type) {
        case 1:
            return {type:"光点",color:"#c4d1df",category:"type_1"}
        case 2:
            return {type:"樱饼",color:"#ffd9fa",category:"type_2"}
    
        default:
            break;
    }
}
//稀有度枚举
export function rarityEnum(rarity){
    switch (rarity) {
        case 1:
            return 'N'
        case 2:
            return 'R'
        case 3:
            return 'SR'
        case 4:
            return 'SSR'
        case 5:
            return 'UR'
    
        default:
            break;
    }
}
