//卡牌枚举
export async function cardEnum(){
    var list = [];
    await axios.get('/GemEnum.xlsx',{responseType:'arraybuffer'}).then((res)=>{
        const XLSX = require('xlsx')
        var data = new Uint8Array(res.data)
        var wb = XLSX.read(data, {type:"array"})
        var sheets = wb.Sheets
        var obj = XLSX.utils.sheet_to_json(sheets.PeopleDeck)
        obj.forEach((item,index)=>{
            const backImageTop = require('@/assets/image/people/' + item.backImageTop + '.png');
            const backImageBottom = require('@/assets/image/deck_back/deck_back_' + item.backImageBottom + '_a.png');
            let backImageDecorate = null
            item.backImageDecorate == 'null' ? '' : backImageDecorate = require('@/assets/image/decorate/decorate_' + item.backImageDecorate + '.png');
            const camp = require('@/assets/image/icon/back_' + item.camp + '.png');
            list.push({id:item.id,backImageTop:backImageTop,backImageBottom:backImageBottom,backImageDecorate:backImageDecorate,camp:camp,price:item.price,currency:currencyEnum(item.currency),chineseName:item.chineseName,foreignName:item.foreignName,deckLv:rarityEnum(item.deckLv),audio:item.audio});
        })
    })
    return list
}
