// 背景图枚举
export async function backEnum(){
    var list = []
    await axios.get('/GemEnum.xlsx',{responseType:'arraybuffer'}).then((res)=>{
        const XLSX = require('xlsx')
        var data = new Uint8Array(res.data)
        var wb = XLSX.read(data, {type:"array"})
        var sheets = wb.Sheets
        var obj = XLSX.utils.sheet_to_json(sheets.CardBack)
        obj.forEach((item,index)=>{
            list.push({id:item.id,imgUrl:require(`@/assets/image/deck_back/deck_back_${item.id}_a.png`),price:item.price,currency:currencyEnum(item.currency)})
            // list[index] = _.merge(item,{imgUrl:require(`@/assets/image/deck_back/deck_back_${item.id}_a.png`)})
        })
        // console.log(XLSX.utils.sheet_to_json(sheets.CardBack));
        // this.transformSheets(sheets)
    })
    return list
}