<template>
    <div class="view-style" :style="`background-image:url(${require('@/assets/image/main_back/home_image_2_a.png')})`">
        <div class="main-style" @click="routerEvent('MainGame')"><i class="icon-back1" style="vertical-align:bottom"/></div>
        <div class="money-style">
            <span style="margin-left:30px;color:#c4d1df">光点
                <span style="margin-left:10px">{{currentArchive.property.type_1}}</span>
            </span>
            <span style="margin-left:30px;color:#ffd9fa">樱饼
                <span style="margin-left:10px">{{currentArchive.property.type_2}}</span>
            </span>
        </div>
        <div class="shop-style">
            <div class="shop-left">
                <div class="shop-type-1" id="shop-type-1" :style="`line-height:${lineHeight.lh1}px${currentTab == 1 ? ';background-color:#f0fff080' : ';background-color:#f0fff033'}`" @click="tabEvent(1)">卡牌</div>
                <div class="shop-type-2" id="shop-type-2" :style="`line-height:${lineHeight.lh1}px${currentTab == 2 ? ';background-color:#f0fff080' : ';background-color:#f0fff033'}`" @click="tabEvent(2)">背景图</div>
                <div class="shop-type-3" id="shop-type-3" :style="`line-height:${lineHeight.lh1}px${currentTab == 3 ? ';background-color:#f0fff080' : ';background-color:#f0fff033'}`" @click="tabEvent(3)">卡牌装饰</div>
            </div>
            <div class="shop-right">
                <div class="shop-item-1" id="shop-item-id1" ref="shop-item-id1" v-if="currentTab == 1" @mousewheel="shopRightWheelEvent">
                    <div class="shop-deck-style" v-for="(item,index) in shopDeckList" :key="item.id">
                        <div class="shop-deck-item-1" :style="`background-image:url(${item.backImageTop}),url(${item.backImageBottom})`"></div>
                        <div class="shop-deck-item-2" :style="`color:${item.currency.color};cursor:${item.price < currentArchive.property[item.currency.category] ? 'pointer' : 'not-allowed'}`" @click="buyDeckEvent($event,item,index)">{{item.price + item.currency.type}}</div>
                        <div class="shop-deck-item-3" :style="`background-image:url(${item.camp})`"></div>
                        <div class="shop-deck-item-4"></div>
                    </div>
                </div>
                <div class="shop-item-2" id="shop-item-id2" ref="shop-item-id2" v-if="currentTab == 2" @mousewheel="shopRightWheelEvent">
                    <div class="shop-deck-style" v-for="(item,index) in shopBackList" :key="item.id">
                        <div class="shop-deck-item-1" :style="`background-image:url(${item.imgUrl})`"></div>
                        <div class="shop-deck-item-2" :style="`color:${item.currency.color};cursor:${item.price < currentArchive.property[item.currency.category] ? 'pointer' : 'not-allowed'}`" @click="buyDeckBackEvent($event,item,index)">{{item.price + item.currency.type}}</div>
                    </div>
                </div>
                <div class="shop-item-3" id="shop-item-id3" ref="shop-item-id3" v-if="currentTab == 3" @mousewheel="shopRightWheelEvent"></div>
            </div>
        </div>
    </div>
</template>

<script>
import {backEnum, cardEnum} from "@/api/enum.js"
import { encryptLong, decryptLong } from '@/utils/jsencrypt';
import {updataArchive} from '@/api/common';
import _ from 'lodash';
// import XLSX from 'xlsx'

export default {
    name:"ShopDeck",
    data(){
        return{
            lineHeight:{
                lh1:0
            },
            shopDeckList:[],
            shopBackList:[],
            currentTab:1,
            currentBorder:{
                borderTop:'2px solid honeydew',
                borderBottom:'2px solid honeydew',
                borderLeft:'2px solid honeydew',
            }
        }
    },
    computed:{
        currentArchive(){
            var currentArchive = {};
            return currentArchive = this.$store.state.archive.archive;
        }
    },
    mounted(){
        this.lineHeightInit();
        this.getDeckList();
        // this.domInit(1);
    },
    methods:{
        routerEvent(e){
            this.$router.push({name:e})
        },
        lineHeightInit(){
            const dom = document.getElementById("shop-type-1");
            this.lineHeight.lh1 = dom.offsetHeight
        },
        // 列举卡牌枚举
        getDeckList(){
            cardEnum().then((res)=>{
                var list = res
                var obj = this.$store.state.archive.archive
                if(obj.own_cards.length == 0){
                    this.shopDeckList = _.cloneDeep(list)
                }else if(obj.own_cards.length < res.length){
                    obj.own_cards.forEach((item,index)=>{
                        _.remove(list,((n)=>{
                            return n.id == item
                        }))
                    })
                    this.shopDeckList = _.cloneDeep(list)
                }else if(obj.own_cards.length == res.length){
                    this.shopDeckList = []
                }
            })
        },
        // 列举卡牌背景枚举
        getDeckBackList(){
            backEnum().then((res)=>{
                var list = res
                var obj = this.$store.state.archive.archive
                if(obj.unlock_background.length == 0){
                    this.shopBackList = _.cloneDeep(list)
                }else if(obj.unlock_background.length < res.length){
                    obj.unlock_background.forEach((item,index)=>{
                        _.remove(list,((n)=>{
                            return n.id == item
                        }))
                    })
                    this.shopBackList = _.cloneDeep(list)
                }else if(obj.own_cards.length == res.length){
                    this.shopBackList = []
                }
            })
        },
        // 初始化鼠标控制x轴滚动条
        shopRightWheelEvent(e){
            if(e.wheelDelta < 0){
                if(this.$refs[`shop-item-id${this.currentTab}`].scrollWidth - this.$refs[`shop-item-id${this.currentTab}`].scrollLeft > 24){
                    this.$refs[`shop-item-id${this.currentTab}`].scrollLeft = this.$refs[`shop-item-id${this.currentTab}`].scrollLeft + 20
                }
            }
            if(e.wheelDelta > 0){
                if(this.$refs[`shop-item-id${this.currentTab}`].scrollLeft > 0){
                    this.$refs[`shop-item-id${this.currentTab}`].scrollLeft = this.$refs[`shop-item-id${this.currentTab}`].scrollLeft - 20
                }
            }
        },
        // 购买卡牌
        buyDeckEvent(e,item,index){
            if(e.srcElement.style.cursor == 'not-allowed'){
                return '';
            }else if(e.srcElement.style.cursor == 'pointer'){
                var archive = this.$store.state.archive.archive;
                archive.own_cards.push(item.id);
                archive.property[item.currency.category] = archive.property[item.currency.category] - item.price
                this.$store.dispatch('Archive',archive)
                // sessionStorage.setItem('Temporary_Archive',encryptLong(JSON.stringify(archive)))
                updataArchive()
                _.pullAt(this.shopDeckList,index)
            }
        },
        //购买背景
        buyDeckBackEvent(e,item,index){
            if(e.srcElement.style.cursor == 'not-allowed'){
                return '';
            }else if(e.srcElement.style.cursor == 'pointer'){
                var archive = this.$store.state.archive.archive;
                archive.unlock_background.push(item.id);
                archive.property[item.currency.category] = archive.property[item.currency.category] - item.price
                this.$store.dispatch('Archive',archive)
                // sessionStorage.setItem('Temporary_Archive',encryptLong(JSON.stringify(archive)))
                updataArchive()
                _.pullAt(this.shopBackList,index)
            }
        },
        // 选项卡事件
        tabEvent(e){
            switch (e) {
                case 1:
                    this.currentTab = 1;
                    this.getDeckList();
                    break;
                case 2:
                    this.currentTab = 2;
                    // console.log(backEnum());
                    this.getDeckBackList();
                    // this.getDeckList();
                    break;
                case 3:
                    this.currentTab = 3;
                    // this.getDeckList();
                    break;
                default:
                    break;
            }
        }
    }
}
</script>

